import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setUserActivityReducer } from "../../store/action/userActivityAction";
import { api_request } from "../../store/action/commonActions";
import {
  login_path,
  method_get,
  user_activity_log_path,
} from "../../config/config";
import {
  GET_USER_LIST_ERROR,
  GET_USER_LIST_LOADING,
  GET_USER_LIST_SUCCESS,
} from "../../store/types/userActivityType";
import Navbar from "../Layouts/Navbar";
import Footer from "../Layouts/Footer";
import { Navigate, useNavigate } from "react-router-dom";

export default function UserList() {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const user = useSelector((state) => state.auth?.user);
  const userList = useSelector((state) => state.userActivity?.userList);

  useEffect(() => {
    dispatch(
      api_request(
        method_get,
        "/userList",
        null,
        {},
        GET_USER_LIST_LOADING,
        GET_USER_LIST_SUCCESS,
        GET_USER_LIST_ERROR,
        user?.accessToken
      )
    );
  }, []);

  const onViewLog = (d) => {
    navigate(user_activity_log_path, { state: { id: d.id } });
  };

  if (!user) {
    return <Navigate to={login_path} />;
  }

  return (
    <>
      <Navbar />
      <div id="userList">
        <div id="userList-card" className="z-depth-1">
          {userList && (
            <table className="table responsive-table centered">
              <thead>
                <tr>
                  <th>ID</th>
                  <th>USER NAME</th>
                  <th>NAME</th>
                  <th>EMAIL</th>
                  <th>VIEW LOG</th>
                </tr>
              </thead>
              <tbody>
                {userList.map((d, i) => (
                  <tr key={i}>
                    <td>{d.id}</td>
                    <td>{d.userName}</td>
                    <td>{d.name}</td>
                    <td>{d.email}</td>
                    <td>
                      <i
                        className="material-icons onHover"
                        onClick={() => onViewLog(d)}
                      >
                        visibility
                      </i>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          )}
        </div>
      </div>
      <Footer />
    </>
  );
}
