import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setSalarydashboard } from "../../../store/action/salaryDashboardAction";
import ReactApexChart from "react-apexcharts";
import LoaderComp from "../../Layouts/LoaderComp";
import { customYAxisFormatter } from "../../../utils/salaryDashboardDataHeler";
import { graph_blue_bar, graph_golden_bar } from "../../../config/config";

export default function MedianSalaryColumnChart() {
  const dispatch = useDispatch();

  const chart2 = useSelector((state) => state.salaryDashboard?.chart2);

  const chart2loading = useSelector(
    (state) => state.salaryDashboard?.chart2loading
  );
  const chart2success = useSelector(
    (state) => state.salaryDashboard?.chart2success
  );
  const chart2error = useSelector(
    (state) => state.salaryDashboard?.chart2error
  );
  const common = useSelector((state) => state.common);

  const [location, setLocation] = useState(null);
  const [data, setData] = useState(null);

  useEffect(() => {
    if (chart2) {
      let d = [];
      let l = [];
      let totalOther = 0;
      let otherCount = 0;
      for (let i = 0; i < chart2?.length; i++) {
        if (chart2[i]?.location?.toLowerCase()?.includes("others")) {
          totalOther = totalOther + Number(chart2[i]?.median);
          otherCount++;
        } else {
          if (Number(chart2[i]?.median) !== 0) {
            l.push(chart2[i]?.location);
            d.push({
              x: chart2[i]?.location,
              y: Number(chart2[i]?.median),
              colors: [
                chart2[i]?.location === common.salaryDashboard?.location
                  ? graph_golden_bar
                  : graph_blue_bar,
              ],
              fillColor:
                chart2[i]?.location === common.salaryDashboard?.location
                  ? graph_golden_bar
                  : graph_blue_bar,
              fill: {
                type: "gradient",
                gradient: {
                  shade:
                    chart2[i]?.location === common.salaryDashboard?.location
                      ? graph_golden_bar
                      : graph_blue_bar,
                  shadeIntensity: 0.1,
                  opacityFrom: 0.8,
                  opacityTo: 0.9,
                  stops: [0, 95, 100],
                },
              },
            });
          }
        }
      }

      if (Math.round(totalOther) !== 0) {
        l.push("Other");
        d.push({
          x: "Other",
          y: Math.round(totalOther / otherCount),
          fillColor: graph_blue_bar,
        });
      }
      setLocation(l);
      setData(d);
      dispatch(setSalarydashboard({ chart2success: false }));
    }
    //eslint-disable-next-line
  }, [chart2success, chart2]);

  useEffect(() => {
    if (chart2loading) {
      setData(null);
      setLocation(null);
    }
  }, [chart2loading]);

  const options = {
    dataLabels: {
      enabled: false,
    },
    chart: {
      toolbar: {
        show: false,
      },
      type: "bar",
      height: 400,
      // Other chart options...
    },
    plotOptions: {
      bar: {
        dataLabels: {
          enabled: false, // Hide the bar labels
        },
      },
    },

    yaxis: {
      labels: {
        formatter: customYAxisFormatter,
        style: {
          colors: "#f7f7f7", // Set the x-axis labels font color
        },
      },
    },
    xaxis: {
      categories: location,
      labels: {
        style: {
          colors: "#000000", // Set the x-axis labels font color
        },
      },
    },
  };

  return (
    <div className="graph z-depth-3">
      {data?.length > 1 && location && !chart2error ? (
        <div className="graph-div">
          <span className="graph-heading">
            {`Median Salary for "${common?.salaryDashboard?.searchRole}" jobs in ${common.salaryDashboard?.location} vs. Across Austarllia`}
          </span>
          <ReactApexChart
            options={options}
            series={[{ name: "Job Salary", data: data }]}
            type="bar"
            height={300}
          />
        </div>
      ) : (
        <div className="graph-nodata-div">
          <h6 className="graph-heading">
            {"Median Salary in " +
              common.salaryDashboard?.location +
              " vs. Across Austarllia"}
          </h6>
          {chart2loading ? <LoaderComp /> : <span>No Data</span>}
        </div>
      )}
    </div>
  );
}
