import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setSalarydashboard } from "../../../store/action/salaryDashboardAction";
import ReactApexChart from "react-apexcharts";
import LoaderComp from "../../Layouts/LoaderComp";
import { customYAxisFormatter } from "../../../utils/salaryDashboardDataHeler";
import {
  graph_blue_bar,
  graph_golden_bar,
  graph_red_bar,
} from "../../../config/config";

const calculatePercentile = (sortedData, percentile) => {
  const index = (percentile / 100) * sortedData.length;
  if (index % 1 === 0) {
    // If the index is a whole number, return the average of the values at the two adjacent positions
    return (
      (sortedData[index - 1]?.average_salary +
        sortedData[index]?.average_salary) /
      2
    );
  } else {
    // If the index is not a whole number, return the value at the rounded index
    return sortedData[Math.floor(index)]?.average_salary;
  }
};

export default function MedianSalaryGauge() {
  const dispatch = useDispatch();
  const [data, setData] = useState(null);
  const [results, setResults] = useState({
    median: null,
    percentile25: null,
    percentile75: null,
  });
  const chart4 = useSelector((state) => state.salaryDashboard?.chart4);

  const chart4loading = useSelector(
    (state) => state.salaryDashboard?.chart4loading
  );
  const chart4success = useSelector(
    (state) => state.salaryDashboard?.chart4success
  );

  const chart4error = useSelector(
    (state) => state.salaryDashboard?.chart4error
  );
  const common = useSelector((state) => state.common);

  useEffect(() => {
    if (chart4) {
      // Sort the data in ascending order
      const sortedData = [...chart4].sort(
        (a, b) => a?.average_salary - b?.average_salary
      );

      // Calculate percentiles
      const median = calculatePercentile(sortedData, 50);
      const percentile25 = calculatePercentile(sortedData, 25);
      const percentile75 = calculatePercentile(sortedData, 75);

      // Update the results object
      setResults({
        median,
        percentile25,
        percentile75,
      });
    }
  }, [chart4, chart4success]);

  const options = {
    dataLabels: {
      enabled: false,
    },
    chart: {
      toolbar: {
        show: false,
      },
      type: "bar",
      height: 400,
      // Other chart options...
    },

    fill: {
      type: "gradient",
      gradient: {
        shadeIntensity: 0.1,
        opacityFrom: 0.8,
        opacityTo: 0.9,
        stops: [0, 95, 100],
      },
    },
    yaxis: {
      labels: {
        formatter: customYAxisFormatter,
        style: {
          colors: "#f7f7f7", // Set the x-axis labels font color
        },
      },
    },
    xaxis: {
      categories: ["25th percentile", "Median", "75th percentie"],
      labels: {
        style: {
          colors: "#000000", // Set the x-axis labels font color
        },
      },
    },
  };

  useEffect(() => {
    if (chart4) {
      let data = [];
      if (
        chart4 &&
        results?.median &&
        results?.percentile25 &&
        results?.percentile75
      ) {
        const quarterPer = results.percentile25;
        const median = results.median;
        const threeQuarterPer = results.percentile75;
        data.push({
          x: "25th percentile",
          y: quarterPer ? Number(quarterPer) : 0,
          fillColor: graph_red_bar,
        });
        data.push({
          x: "Median",
          y: median ? Number(median) : 0,
          fillColor: graph_golden_bar,
        });
        data.push({
          x: "75th percentile",
          y: threeQuarterPer ? Number(threeQuarterPer) : 0,
          fillColor: graph_blue_bar,
        });
        setData(data);
      }
      dispatch(setSalarydashboard({ chart4success: false }));
    }
    //eslint-disable-next-line
  }, [chart4success, chart4, results]);

  useEffect(() => {
    if (chart4loading) {
      setData(null);
    }
  }, [chart4loading]);

  return (
    <div className="graph z-depth-3">
      {data?.length === 3 &&
      data[0].y !== null &&
      data[0].y !== undefined &&
      data[0].y !== 0 &&
      data[1].y !== null &&
      data[1].y !== undefined &&
      data[1].y !== 0 &&
      data[2].y !== null &&
      data[2].y !== undefined &&
      data[2].y !== 0 &&
      !chart4error ? (
        <div className="graph-div">
          <span className="graph-heading">
            {`25th-Median-75th-percentile Salary for "${common?.salaryDashboard?.searchRole}" jobs in ${common.salaryDashboard?.location}`}
          </span>
          <ReactApexChart
            options={options}
            series={[{ name: "Job Salary", data: data }]}
            type="bar"
            height={300}
          />
        </div>
      ) : (
        <div className="graph-nodata-div">
          <h6 className="graph-heading">
            {"25th-Median-75th-percentile Salary in " +
              common.salaryDashboard?.location}
          </h6>
          {chart4loading ? <LoaderComp /> : <span>No Data</span>}
        </div>
      )}
    </div>
  );
}
