import React, { useEffect, useState } from "react";
import Chart from "react-apexcharts";
import { useSelector } from "react-redux";
import LoaderComp from "../../Layouts/LoaderComp";

export default function Top5CategoriesPieChart() {
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    // Check if the screen width is less than a certain value (e.g., 768px) to determine if it's a mobile device
    const handleResize = () => {
      setIsMobile(window.innerWidth < 768);
    };

    // Add an event listener to handle window resizing
    window.addEventListener("resize", handleResize);

    // Initial check
    handleResize();

    // Clean up the event listener when the component unmounts
    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, []);

  const chart8 = useSelector((state) => state.salaryDashboard?.chart8);

  const chart8loading = useSelector(
    (state) => state.salaryDashboard?.chart8loading
  );
  const chart8success = useSelector(
    (state) => state.salaryDashboard?.chart8success
  );
  const chart8error = useSelector(
    (state) => state.salaryDashboard?.chart8error
  );
  const common = useSelector((state) => state.common);

  const handleDataLabelVisibility = (val, opts) => {
    if (val < 5) {
      return "";
    } else {
      return opts.w.globals.labels[opts.seriesIndex];
    }
  };

  const [options, setOptions] = useState({
    chart: {
      type: "pie",
    },
    labels: [],
    legend: {
      position: "bottom",
      itemMargin: {
        horizontal: 5, // Set the horizontal spacing between legend items
        vertical: 10, // Set the vertical spacing between legend items
      },
    },

    plotOptions: {
      pie: {
        dataLabels: {
          formatter: handleDataLabelVisibility,
          offset: -15, // Adjust the position of the data labels
          minAngleToShowLabel: 10, // Minimum angle to show data label (optional)
          style: {
            fontSize: "16px",
          },
        },
      },
    },

    responsive: [
      {
        breakpoint: 480,
        options: {
          legend: {
            position: "bottom",
          },
        },
      },
    ],
  });

  const updateOptions = () => {
    setOptions({
      chart: {
        type: "pie",
      },
      labels: [],
      legend: {
        position: "bottom",
        itemMargin: {
          horizontal: 5, // Set the horizontal spacing between legend items
          vertical: 10, // Set the vertical spacing between legend items
        },
      },

      plotOptions: {
        pie: {
          dataLabels: {
            formatter: handleDataLabelVisibility,
            offset: -15, // Adjust the position of the data labels
            minAngleToShowLabel: 10, // Minimum angle to show data label (optional)
            style: {
              fontSize: "16px",
            },
          },
        },
      },

      responsive: [
        {
          breakpoint: 480,
          options: {
            legend: {
              position: "bottom",
            },
          },
        },
      ],
    });
  };

  useEffect(() => {
    updateOptions();
  }, [chart8]);
  // Call the updateOptions function when you want to update the options

  const [series, setSeries] = useState([]);

  useEffect(() => {
    if (chart8) {
      const categoryCounts = {};

      const filteredChart8 = chart8.filter((d) => d.category !== null);

      // Count the number of jobs in each category
      filteredChart8.forEach((job) => {
        const category = job.category;
        categoryCounts[category] = (categoryCounts[category] || 0) + 1;
      });

      // Sort the categories based on counts in descending order
      const sortedCategories = Object.keys(categoryCounts).sort(
        (a, b) => categoryCounts[b] - categoryCounts[a]
      );

      // Select the top 5 categories
      const top5Categories = sortedCategories.slice(0, 5);

      // Update options and series based on the top 5 categories
      setOptions({
        ...options,
        labels: top5Categories,
      });

      setSeries(top5Categories.map((category) => categoryCounts[category]));
    }
    //eslint-disable-next-line
  }, [chart8success, chart8]);

  useEffect(() => {
    if (chart8loading) {
      setSeries([]);
    }
  }, [chart8loading]);

  return (
    <div className="graph z-depth-3 ">
      {series.length > 0 && !chart8error ? (
        <div className={isMobile ? "graph-div" : ""}>
          <span className="graph-heading">
            {`Distribution of Jobs in Top Common Categories in ${common.salaryDashboard?.location}`}
          </span>
          <Chart options={options} series={series} type="pie" width={420} />
        </div>
      ) : (
        <div className="graph-nodata-div">
          <h6 className="graph-heading">
            {`Distribution of Jobs in Top Common Categories in ${common.salaryDashboard?.location}`}
          </h6>

          {chart8loading ? <LoaderComp /> : <span>No Data</span>}
        </div>
      )}
    </div>
  );
}
